import {Component, ElementRef, Renderer2, ViewChild} from '@angular/core';
import {AlertController, IonicPage, LoadingController, NavController, Platform, Searchbar} from 'ionic-angular';
import {PAGES1} from '../../../app/global.setting';
import {Geolocation} from '@ionic-native/geolocation';
import {UserService} from "../../../providers/user.service";
import {ArticleService} from "../../../providers/article.service";
import {Service} from "../../../providers/model/service";
import {GoogleMap, GoogleMapOptions, GoogleMapsEvent, HtmlInfoWindow} from "@ionic-native/google-maps";
import {MapServiceProvider} from "../../../providers/map.service";


@IonicPage()
@Component({
  selector: 'page-homev1',
  templateUrl: 'homev1.html'
})
export class Homev1Page {
  @ViewChild('map') mapElement: ElementRef;
  @ViewChild('searchbar') searchbar:Searchbar;


  pages:any[];

  map: GoogleMap;
  title: string = 'NeoPlace';
  zoom: number;

  // initial center position for the map
  lat: number;
  lng: number;

  valueSearch: string;
  valueSearchBar: string;
  isDisplaySearch: boolean;
  onFocusSearch: boolean;

  markers = [];
  alive: boolean = true;

  constructor(
    private mapServiceProvider: MapServiceProvider,
    private renderer: Renderer2,
    public loadingCtrl: LoadingController,
    private userService: UserService,
    private articleService: ArticleService,
    public platform: Platform,
    public geolocation: Geolocation,
    public alertCtrl: AlertController,
    public navCtrl: NavController ) {

    //Main Menu
    this.pages = PAGES1;
  }

  ionViewWillEnter() {
    this.platform.ready().then(() => {
        if (this.platform.is('android')) {
        if(!this.mapServiceProvider.map) {
          setTimeout(this.loadMapNative.bind(this), 1000);
        } else {
          this.mapServiceProvider.map.setDiv(this.mapElement.nativeElement);
          this.mapServiceProvider.displayServices();
          this.mapServiceProvider.hideArticles();
          if (this.valueSearch) {
            this.valueSearchBar = this.valueSearch;
            this.mapServiceProvider.findService(this.valueSearch);
          }
          //this.mapServiceProvider.hideFiats();
          if(this.mapServiceProvider.markersService.length == 0) {
            this.loadServiceNative();
          }
        }
      } else {
        this.zoom = 8;
        // initial center position for the map
        this.lat = 0;
        this.lng = 0;

        this.loadMap();
        this.loadService();
        //this.loadFiatPosition();
      }
    }).catch(() => {
      //console.log("Platform error");
    });
  }

  ionViewWillLeave() {
    this.alive = false;
    this.mapServiceProvider.findService('');
    this.isDisplaySearch = false;
    // if (this.platform.is('cordova')) {
    //   this.mapServiceProvider.setDiv(null, null);
    // }
  }

  displaySearchBar() {
    this.isDisplaySearch = !this.isDisplaySearch;
    if(this.isDisplaySearch) {
      setTimeout(() => {
        this.searchbar.setFocus();
      });
    }
  }

  loadMapNative() {
    let loader = this.loadingCtrl.create({content: 'Please wait...', duration: 5000});
    loader.present();
    this.geolocation.getCurrentPosition({ maximumAge: 300000, timeout: 5000, enableHighAccuracy: true })
      .then((position) => {
        let mapOptions: GoogleMapOptions = {
          camera: {
            target: {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            },
            zoom: 5,
            tilt: 30
          },
          controls: {
            myLocationButton: true
          }
        };

        this.mapServiceProvider.setDiv(this.mapElement.nativeElement, mapOptions).then(() => {
          //add markers, polyline, etc.
          loader.dismiss().catch();
          // Now you can use all methods safely.
          this.loadServiceNative();
        });

      }).catch(error => {
        let mapOptions: GoogleMapOptions = {
          camera: {
            target: {
              lat: 0,
              lng: 0
            }
          }
        };

        this.mapServiceProvider.setDiv(this.mapElement.nativeElement, mapOptions).then(() => {
          //add markers, polyline, etc.
          loader.dismiss().catch();
          // Now you can use all methods safely.
          this.loadServiceNative();
        });
    });
  }

  showList(pages) {
    if(pages.page == "TabsPage") {
      this.navCtrl.setRoot(pages.page);
    } else {
      this.navCtrl.push(pages.page);
    }
  }

  showPage(page) {
    this.navCtrl.push(page);
  }

  loadServiceNative() {
    this.articleService.getServices().takeWhile(() => this.alive).subscribe(articles => {
      articles.forEach(article => {{
        if(article.uid != this.userService._userFirebase.uid) {
          this.mapServiceProvider.map.addMarker({
            icon: 'red',
            animation: 'DROP',
            position: {
              lat: article.latitude,
              lng: article.longitude
            }
          })
            .then(marker => {
              this.mapServiceProvider.markersService.push({service: article, marker: marker});

              let listenRender;

              marker.on(GoogleMapsEvent.MARKER_CLICK)
                .subscribe(() => {
                  var htmlInfoWindow = new HtmlInfoWindow();
                  let html = [
                    "<div class=\"pin\" style=\"width:150px;\"" +  "id=\"" + article.$key + "\">",
                    "<ion-card>",
                    "<div class=\"pin\" style=\"height:113px\">",
                    "<img src=" + article.taked_picture + ">",
                    "</div>",
                    "</ion-card>",
                    "<div class=\"post-description\">",
                    "<strong>" + article.title + "</strong> <br><a>See service</a>",
                    "</div>",
                    "</div>"
                  ].join("");

                  htmlInfoWindow.setContent(html);

                  htmlInfoWindow.open(marker);

                  listenRender = this.renderer.listen(this.mapElement.nativeElement, 'click', (evt) => {
                    this.seeDetailsService(article);
                  });
                });

              marker.on(GoogleMapsEvent.INFO_CLOSE)
                .subscribe(() => {
                  listenRender();
                });

            });
        }
      }
      })
    });
  }

  loadFiatPosition() {
    this.userService.getPositions().takeWhile(() => this.alive).subscribe(positions => {
      positions.forEach(position => {
        this.userService.getProfil(position.$key).subscribe(profil => {
          this.addPositionFiat(position.lat, position.lng, profil, position.$key)
        });
      });
    })
  }

  loadService() {
    this.articleService.getServices().takeWhile(() => this.alive).subscribe(services => {
      services.forEach(services => {{
        //if(services.uid != this.userService._userFirebase.uid) {
        this.addMarkerService(services);
        //}

      }
      })
    });
  }

  findService(text1: string) {
    this.markers.forEach(marker => {
      if(text1 && text1.trim() != '' && marker.service) {
        let text = text1.toLowerCase();

        if(marker.service.title.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else if(marker.service.type.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else if(marker.service.description.toLowerCase().indexOf(text) >= 0) {
          marker.visible = true;
        } else {
          marker.visible = false;
        }
      } else {
        marker.visible = true;
      }
    });
  }

  seeDetails(item) {
    this.navCtrl.push('ArticlePage',
      {article: item, key: item.$key});
  }

  seeDetailsService(item) {
    this.navCtrl.push('ServicePage',
      {service: item, key: item.$key});
  }

  loadMap(){
    let loader = this.loadingCtrl.create({content: 'Please wait...', duration: 10000});
    loader.present();
    this.geolocation.getCurrentPosition()
      .then((position) => {
        loader.dismiss().catch();

        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;

      }).catch((error) => {
      //console.log('Error : ', error);
      const alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Error while getting your position, try again',
        buttons: ['Close']
      });
      alert.present();
    });
  }

  addPositionFiat(lat, lng, profil, uid) {
    this.markers.push({
      lat: lat,
      lng: lng,
      draggable: false,
      position: {profil: profil, uid: uid}
    })
  }

  addMarkerService(service: Service) {
    this.markers.push({
      lat: service.latitude,
      lng: service.longitude,
      draggable: false,
      service: service
    })
  }

  contact(profil, uid) {
    let uidArray = [this.userService._userFirebase.uid, uid];
    uidArray = uidArray.sort();
    this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: uid});
  }

  getItems(ev) {
    // set val to the value of the ev target
    var val = ev.target.value;
    this.valueSearch = val;

    if (this.platform.is('cordova')) {
      this.mapServiceProvider.findService(val);
    } else {
      this.findService(val);
    }
  }
}



